@import url('https://fonts.googleapis.com/css2?family=Montserrat:wght@400;800&family=Roboto&display=swap');

html, body {
    background: #222;
    height: 100%;
    margin: 0;
    color: #eee;
    font-family: "Roboto", sans-serif;
}

h1, h2 {
    text-align: center;
    padding: 0 2rem;
    font-family: "Montserrat", sans-serif;
}

h2 {
    font-size: 1.2rem;
}

p {
    padding: 0 2rem;
    line-height: 1.4;
    margin-bottom: 1.6rem;
}

.uploader {
    height: 33vh;
    width: 100%;
    background: #555;
}

button {
    border: 1px solid #888;
    padding: 2px 5px;
    background: transparent;
    color: #aaa;
    cursor: pointer;
}

.avatar-renders {
    display: flex;
    justify-content: center;
    padding-top: 1rem;
}

.avatar-renders canvas {
    width: 120px;
    height: 120px;
}

img {
    display: block;
}

.controls {
    list-style: none;
    padding: 1rem;
    margin: 0;
    text-align: center;

    li {
        display: inline-block;
        margin-right: .5rem;
    }
}

@media only screen and (min-width: 812px) {
    .draw-area {

    }
}
